@import url("https://fonts.googleapis.com/css?family=Kaushan+Script&display=swap");
@keyframes anim-1 {
  0%, 8.3% {
    left: -100%;
    opacity: 0; }
  8.3%, 25% {
    left: 20%;
    opacity: 1; }
  33.33%, 100% {
    left: 110%;
    opacity: 0; } }

@keyframes anim-2 {
  0%, 33.33% {
    left: -100%;
    opacity: 0; }
  41.63%, 58.29% {
    left: 20%;
    opacity: 1; }
  66.66%, 100% {
    left: 110%;
