.form-wrapper {
  position: relative;
  width: 100%;
  margin: .5em;
  padding: .3em;
  display: flex;
  flex-flow: column; }
  .form-wrapper > .form-content {
    display: flex;
    flex-flow: column;
    margin: .2em;
    flex-flow: row;
    justify-content: space-around; }
    .form-wrapper > .form-content > .form-section {
      display: flex;
      flex-flow: column;
      justify-content: stretch;
      align-items: stretch;
      margin: .1em .7em;
      padding: .1em; }
  .form-wrapper > .submit {
    display: inline-block;
    padding: 15px 25px;
    font-family: "Kaushan Script", cursive;
    font-size: 24px;
    margin: 50px;
    cursor: pointer;
    text-align: center;
    text-decoration: none;
    outline: none;
    color: #fff;
    background-color: #BB0A30;
    border: none;
    border-radius: 15px;
    box-shadow: 0 9px 15px rgba(0, 0, 0, 0.1); }
    .form-wrapper > .submit:hover {
      background-color: #83000A; }
