@import url("https://fonts.googleapis.com/css?family=Kaushan+Script&display=swap");
@keyframes anim-1 {
  0%, 8.3% {
    left: -100%;
    opacity: 0; }
  8.3%, 25% {
    left: 20%;
    opacity: 1; }
  33.33%, 100% {
    left: 110%;
    opacity: 0; } }

@keyframes anim-2 {
  0%, 33.33% {
    left: -100%;
    opacity: 0; }
  41.63%, 58.29% {
    left: 20%;
    opacity: 1; }
  66.66%, 100% {
    left: 110%;
    opacity: 0; } }

@keyframes anim-3 {
  0%, 66.66% {
    left: -100%;
    opacity: 0; }
  74.96%, 91.62% {
    left: 20%;
    opacity: 1; }
  100% {
    left: 110%;
    opacity: 0; } }

input[type=text] {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
  border: none;
  border-bottom: 2px solid #BB0A30;
  border-radius: 5%;
  box-shadow: 0 9px 15px rgba(0, 0, 0, 0.1); }
